
import { defineComponent } from "vue"
import axios from 'axios'

export default defineComponent({
    name: 'ProjectsSection',
    data() {
        return{
            projects: [],
        }
    },
    methods: {
        async fetchProjects(){
            await axios.get('/api/projects').then((data) => {
                this.projects = data.data
            })
        }
    },
    mounted() {
        this.fetchProjects()
    },
})
